
import { Component, OnInit, Input, Output } from '@angular/core';
import { ApplyCustUnApplyedSearchBean } from './bean/applyCustUnApplyedSearch.bean';  //()
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
// import { ProdHoldLoan } from '../../../../constant/custOperation.codeValue'
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router'
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { Gender } from '../../../../constant/custOperation.codeValue'
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { checkChinese } from "app/@uisftech/common/service/validator";

/**
 * 
 */
@Component({
	selector: 'app-custom-dereliction-apply',
	templateUrl: './custom-dereliction-apply.component.html',
	styleUrls: ['./custom-dereliction-apply.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomDerelictionApply implements OnInit {
	// 
	tabMesg: any = tableMessage;
	customerSearch: string = "";
	customerQuery: string = "";
	custNo: any = [];
	arr: any = [];
	data: any;				//
	checkValue: any = [];
	custName: any; 			//
	ConditionTypeId: any;	//
	userform: FormGroup;
	ProdHoldLoan: any = []; //
	gender: any = [];	//	
	total: number;			//	
	pageSize: number = 10;  //	
	msgs: Message[] = [];	//
	applyCustUnApplyedSearchBean: ApplyCustUnApplyedSearchBean = new ApplyCustUnApplyedSearchBean();//()
	//
	code: any;

	constructor(
		public httpService: CusOperationHttpService,
		public fb: FormBuilder,
		public commfunc: Commfunc,
		public router: Router
	) {
		this.codeValues() //
		//,
		this.ProdHoldLoan = this.code['ProdHoldLoan'];
		this.gender = this.code['Gender'];
	}

	ngOnInit() {
		this.applyCustUnApplyedSearchBean.pageSize = 10;
		this.applyCustUnApplyedSearchBean.pageNum = 1;
		this.userform = this.fb.group({
			//-
			// 'custName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/)]),
			// // 'custNo':['', Validators.required],	 //
			// idNo: ['', [Validators.pattern(/^[\w]$/)]],//
			// cardNo: ['', [Validators.pattern(/^[0-9]*$/)]],///^[0-9]*$/
			// loadAcctNo: ['', [Validators.pattern(/^[\w]$/)]],//

			custName: ['', Validators.required],
			// 'custNo':['', Validators.required],	 //
			idNo: [''],//
			cardNo: [''],///^[0-9]*$/
			loadAcctNo: [''],//
		});
		// this.check();
	}

	//
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}

	//
	check() {
		// ;
		// if(this.applyCustUnApplyedSearchBean.idType[0]){
		// 	alert('')
		// }else if(!this.applyCustUnApplyedSearchBean.idType&&this.applyCustUnApplyedSearchBean.cardNo&&this.applyCustUnApplyedSearchBean.loadAcctNo){

		// 	alert('')
		// }

		//
		for (const i in this.userform.controls) {
			this.userform.controls[i].markAsDirty();
		}
		//
		for (const i in this.userform.controls) {
			if (!this.userform.controls[i].valid) {
				return;
			}
		}
		let custInfo = this.httpService.applyCustUnApplyedSearch(this.applyCustUnApplyedSearchBean);
		custInfo.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.data = data.applyCustSearchInfo;
				this.total = data.totalNum;
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '', detail: '' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	// 
	query() {
		this.httpService.applyCustUnApplyedSearch(this.applyCustUnApplyedSearchBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.data = data.applyCustSearchInfo;
				this.total = data.totalNum;
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '', detail: '' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	// 
	again() {
		this.applyCustUnApplyedSearchBean = new ApplyCustUnApplyedSearchBean();
	}

	//
	apply() {
		let distributeRelationModifyInfo = []
		this.checkValue.forEach((i) => {
			let obj = {};
			obj['custName'] = this.data[i].custName
			obj['custNo'] = this.data[i].custNo
			distributeRelationModifyInfo.push(obj)
		});
		let ob = this.httpService.applyCustUnApplyed({ distributeRelationModifyInfo: distributeRelationModifyInfo }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.data = data.distributeRelationModifyInfo;
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '', detail: '' });
				this.query()
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
		this.checkValue = '';
	}

	//
	paginate(event) {
		//
		this.applyCustUnApplyedSearchBean.pageSize = event.rows;
		//
		let currentPage = event.page + 1;
		this.applyCustUnApplyedSearchBean.pageNum = currentPage;
		//
		this.check();
		this.applyCustUnApplyedSearchBean.pageNum = 1;
	}
	//360
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}
}
