import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_CUS } from 'app/@uisftech/common/service/http.constant';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-custom-batch-apply',
  templateUrl: './custom-batch-apply.component.html',
  styleUrls: ['./custom-batch-apply.component.scss'],
  providers: [CusOperationHttpService]

})
export class CustomBatchApply implements OnInit {
  files: any = [];
  fileList: any = [];//
  fileUrl: string;
  fileName: string;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOAD;
  //
  msgs: Message[] = [];
  //
  headerTitle: string = "";
  //
  display: boolean = false;
  showModel: string;
  //
  detailValue: string;
  //
  code: any;

  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //
		//,
  }

  ngOnInit() {
   
  }

  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }

  load(event) {
    for (let file of event.files) {
      this.files.push(file)
    }
    if (event.xhr.response) { //   
      // 
      this.headerTitle = '';
      this.display = true;
      this.showModel = '1';
      this.fileList = JSON.parse(event.xhr.response);
      this.detailValue = this.fileList;
    }
    // let param = { fileList: this.fileList }
    // this.cusOperationHttpService.applyCustBatch(param).subscribe(data => {

    //   if (data.returnCode.code == 'success') {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'success', summary: '', detail: '' });
    //   } else {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
    //   }
    // }, error => {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '', detail: '' });
    // })
  }
  //
  detail(item) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '1';
    this.detailValue = item;
    //   
  }
  detailCall(param) {
    this.display = param;
    //   this.query();
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '', detail: '' });
  }
}
