import { Component, OnInit } from '@angular/core';
import { MyApplyedCustQryBean } from './bean/myApplyedCustQry.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
// import { IsStatusApply, IsStatusApplyList, IsStatusQueryA } from './../../../../constant/custOperation.codeValue';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';

@Component({
	selector: 'app-my-apply',
	templateUrl: './my-apply.component.html',
	styleUrls: ['./my-apply.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class MyApply implements OnInit {

	myApplyedCustQryBean: MyApplyedCustQryBean = new MyApplyedCustQryBean;
	totalNum: number = 0;
	tableData: any = [];
	tabMesg: any = tableMessage;
	first: number = 0;//
	startDate: Date = null;
	endDate: Date = null;
	// isStatusQuery = IsStatusQueryA;//
	// isStatus: any[] = IsStatusApply;  //-
	// isStatusList: any[] = IsStatusApplyList;  //-
	isStatusQuery: any = [];//
	isStatus: any = [];  //-
	isStatusList: any = [];  //-
	msgs: Message[] = [];		//
	ch: any = CALENDAR_CN;
	display: boolean;
	inValue: any;
	headerTitle: string;
	user: any;
	orgId: string = '';
	userId: string = '';
	//
	code: any;

	s1: string = '00';//
	s2: string = '0';//
	s3: string = '01';//
	s4: string = '1';//

	constructor(
		public httpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public router: Router
	) {
		this.codeValues() //
		//,
		this.isStatusQuery = this.code['IsStatusQueryA'];
		this.isStatus = this.code['IsStatusApply'];
		this.isStatusList = this.code['IsStatusApplyList'];

		this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
	}

	ngOnInit() {
		this.myApplyedCustQryBean.pageSize = 10;
		this.myApplyedCustQryBean.pageNum = 1;
	}

	//
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code
	}

	//-
	querySecle() {
		this.myApplyedCustQryBean.pageSize = 10;
		this.myApplyedCustQryBean.pageNum = 1;
		this.first = 0;
		this.searchBtn();
	}

	//  
	searchBtn() {

		//
		if (this.myApplyedCustQryBean.custNo == '') {
			this.myApplyedCustQryBean.custNo = undefined;
		}
		var reg = /^[0-9]*$/;
		if (this.myApplyedCustQryBean.custNo != undefined) {
			if (this.myApplyedCustQryBean.custNo != '') {
				if (!reg.test(this.myApplyedCustQryBean.custNo)) {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: '' });
					return
				}
			}
		}

		//
		if (this.myApplyedCustQryBean.custName == '') {
			this.myApplyedCustQryBean.custName = undefined;
			this.myApplyedCustQryBean.custName_LIKE = undefined;
		}
		var reg1 = /^[\u4e00-\u9fa5]{0,}$/;
		if (this.myApplyedCustQryBean.custName != undefined) {
			if (this.myApplyedCustQryBean.custName != '') {
				if (!reg1.test(this.myApplyedCustQryBean.custName)) {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '', detail: '' });
					return
				}
			}
		}

		if (this.myApplyedCustQryBean.statusId == '') {
			this.myApplyedCustQryBean.statusId = undefined;
		}
		if (this.myApplyedCustQryBean.custName != undefined) {
			this.myApplyedCustQryBean.custName_LIKE = '%' + this.myApplyedCustQryBean.custName + '%';
		}
		if (!this.myApplyedCustQryBean.stu) {
			let arr = [];
			arr.push('00');
			arr.push('01');
			arr.push('0');
			arr.push('1');
			this.myApplyedCustQryBean.statusId_IN = arr;
		} else {
			if (this.myApplyedCustQryBean.stu == '00') {
				let arr = [];
				arr.push('00');
				this.myApplyedCustQryBean.statusId_IN = arr;
			}
			if (this.myApplyedCustQryBean.stu == '0') {
				let arr = [];
				arr.push('0');
				arr.push('01');
				this.myApplyedCustQryBean.statusId_IN = arr;
			}
			if (this.myApplyedCustQryBean.stu == '1') {
				let arr = [];
				arr.push('1');
				this.myApplyedCustQryBean.statusId_IN = arr;
			}
		}

		this.myApplyedCustQryBean.creater = this.user.userId;
		this.myApplyedCustQryBean.operationType = '2';
		this.myApplyedCustQryBean.createDate_GE = this.commfunc.transDateN(this.startDate);
		this.myApplyedCustQryBean.createDate_LE = this.commfunc.transDateN(this.endDate);
		if (this.myApplyedCustQryBean.createDate_GE == "" || this.myApplyedCustQryBean.createDate_GE == null) {
			this.myApplyedCustQryBean.createDate_GE = undefined;
		}
		if (this.myApplyedCustQryBean.createDate_LE == "" || this.myApplyedCustQryBean.createDate_LE == null) {
			this.myApplyedCustQryBean.createDate_LE = undefined;
		}
		this.httpService.queryMainContactChangeByAnyKey(this.myApplyedCustQryBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.tableData = data.resultList;
				this.totalNum = data.total;
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	//360
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}

	//
	resetBtn() {
		this.myApplyedCustQryBean = new MyApplyedCustQryBean;
		this.myApplyedCustQryBean.pageSize = 10;
		this.myApplyedCustQryBean.pageNum = 1;
		this.startDate = null;
		this.endDate = null;
		this.first = 0;
	}
	//
	paginate(event) {
		this.myApplyedCustQryBean.pageSize = event.rows * 1;
		let currentPage = event.page + 1;
		this.myApplyedCustQryBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.myApplyedCustQryBean.pageSize;
		this.searchBtn();
	}

	//
	showDataStart() {
		if (this.startDate && this.endDate) {
			if (this.startDate > this.endDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: '' });
				setTimeout(() => {
					this.startDate = null;
				})
			}
		}
	}
	//
	showDataEnd() {
		if (this.startDate && this.endDate) {
			if (this.startDate > this.endDate) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: '' });
				setTimeout(() => {
					this.endDate = null;
				})
			}
		}
	}

	//
	doDetail(data) {
		this.display = true;
		this.inValue = _.clone(data.batchNo);
		this.headerTitle = '';
	}

	//
	doDetailClose() {
		this.display = false;
	}
	//
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
	// m(e) {
	// 	this.productMetric = this.productMetric1.slice(row * page, row * (e.page + 1));
	// }

	compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }
}
