import { Component, OnInit } from '@angular/core';
import { ApplyCustWaitingApproveQryBean } from './bean/applyCustWaitingApproveQry.bean';//
import { ApplyCustWaitingApproveDetailBean } from './bean/applyCustWaitingApproveDetail.bean';//
import { ApplyCustApproveBean } from './bean/applyCustApprove.bean';//
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
// import { IsAlloat } from './../../../../constant/custOperation.codeValue';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
/**
 * 
 */
@Component({
	selector: 'pending-apply',
	templateUrl: './pending-apply.component.html',
	styleUrls: ['./pending-apply.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class PendingApply implements OnInit {
	applyedDate:string;
	public search: string = '';
	public customerQuery: string = "";
	public data1: any;
	public lookOver: boolean = false;// dialog
	public addUpHeader: any;//dialog 
	public applyedDateFrom: any //
	public applyedDateTo: any //
	public ch: any = CALENDAR_CN;
	public temData: Date = new Date();
	public startDate: string;
	public endDate: string;
	public calendar: any = [];
	public checkValue: any = [];
	public data: any;//
	// public distributeCustQryForm :  FormGroup;
	public checkValueList: Array<any> = [];
	//
	public total: number;
	//
	public pageSize: number = 10;
	//
	public msgs: Message[] = [];
	// public totalNum:any;
	public applyCustApproveBean: ApplyCustApproveBean = new ApplyCustApproveBean();//
	public applyCustWaitingApproveQryBean: ApplyCustWaitingApproveQryBean = new ApplyCustWaitingApproveQryBean();//
	public applyCustWaitingApproveDetailBean: ApplyCustWaitingApproveDetailBean = new ApplyCustWaitingApproveDetailBean();//
	// public isAllot:any[] = IsAlloat;//    
	public list: Array<ApplyCustApproveBean> = [];
	// public list:any =[{applyCustApproveInfo:'oldOrgId'},{applyeResult:''}]
	
	constructor(
		public httpService: CusOperationHttpService,
		public fb: FormBuilder,
		public commfunc: Commfunc,
		public router: Router
	) {
		// this.distributeCustQryForm = fb.group({
		// 	applyedDateFrom :['', Validators.required],	
		// 	applyedDateTo:['', Validators.required],	
		// 	applyStatus:['', Validators.required],	
		// 	custNo:['', Validators.required],	  
		// 	custName:['', Validators.required],	
		// 	pageNum:['', Validators.required],	
		// 	pageSize:['', Validators.required],       
		// })
	}
	ngOnInit() {
		this.applyCustWaitingApproveQryBean.pageSize = 10;
		this.applyCustWaitingApproveQryBean.pageNum = 1;
		this.query();
	}
	// 
	query() {
		let param = {}
		this.applyCustWaitingApproveQryBean.applyedDateFrom = this.commfunc.transDateN(this.applyedDateFrom);
		this.applyCustWaitingApproveQryBean.applyedDateTo = this.commfunc.transDateN(this.applyedDateTo);
		this.httpService.applyCustWaitingApproveQry(param).subscribe(data => {
			this.data = data.myApplyedCustQryInfo;
		})
	}
	//
	check(car) {
		this.addUpHeader = '';
		this.lookOver = true;
		//console.log(car)
		this.httpService.applyCustWaitingApproveDetailQry(car).subscribe(data => {
			if (data.errorMessage == null) {
				this.data1 = data.myApplyedCustQryInfo;
				this.total = data.totalNum;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	// //
	// showdata(event) {
	// 	//console.log(event);
	// 	this.startDate = this.commfunc.transDateN(event[0]);//
	// 	this.endDate = this.commfunc.transDateN(event[1]);//
	// 	this.calendar = [this.startDate, this.endDate];
	// 	console.log(this.calendar)
	// 	this.applyCustWaitingApproveQryBean.applyedDateFrom = this.startDate;
	// 	this.applyCustWaitingApproveQryBean.applyedDateTo = this.endDate;
	// 	//console.log(this.distributeCustQryBean)
	//   }
	//
	again() {
		this.applyCustWaitingApproveQryBean = new ApplyCustWaitingApproveQryBean();
	}

	//
	exam() {
		let ob = this.httpService.applyCustWaitingApproveQry(this.applyCustWaitingApproveQryBean);
		ob.subscribe(data => {
			this.data = data.myApplyedCustQryInfo;
			this.total = data.totalNum;
		}, err => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		})
	}

	//
	ratify() {
		let param = { applyCustApproveInfo: this.checkValueList, applyeResult: '1' };
		let ob = this.httpService.applyCustApprove(param);
		ob.subscribe(data => {
			this.data = data.applyCustApproveFail;
			this.exam()
		})
	}
	//
	noratify() {
		let param = { applyCustApproveInfo: this.checkValueList, applyeResult: '2' };
		let ob = this.httpService.applyCustApprove(param);
		ob.subscribe(data => {
			this.data = data.applyCustApproveFail;
			this.exam()
		})
	}
	//
	change(car, event) {
		/**
		 * event = true checkValueList
		 * event = false checkValueList
		 */

		if (event) {
			this.checkValueList.push(car);
		} else {
			this.checkValueList.splice(this.checkValueList.indexOf(car), 1);
		}
	}
	//
	paginate(event) {
		//
		this.applyCustWaitingApproveQryBean.pageSize = event.rows;
		//
		let currentPage = event.page + 1;
		this.applyCustWaitingApproveQryBean.pageNum = currentPage;
		//
		this.exam();
		this.applyCustWaitingApproveQryBean.pageNum = 1;
	}
	paginates(event) {
		this.applyCustWaitingApproveDetailBean.pageSize = event.rows;
		let currentPage = event.page + 1;
		this.applyCustWaitingApproveDetailBean.pageNum = currentPage;
		//
		this.query();
		this.applyCustWaitingApproveDetailBean.pageNum = 1;
	}
	// 360
	toCustView(data) {
		this.commfunc.setSessionData('custNo', data.custNo);
		this.router.navigate(['pages/custom-view']);
	}
	//
	showData() {
		this.temData = this.applyedDateFrom;
	}
}
